const _DUMMY_H264_IDR_FRAME_SLICE_HDR0 = new Uint8Array([0x20, 0x00, 0xcb]);
const _DUMMY_H264_IDR_FRAME_SLICE_HDR1 = new Uint8Array([0x10, 0x00, 0x32]);
let _h264IdrPicIdToggle = false;

function _dummyH264Frame() {
  _h264IdrPicIdToggle = !_h264IdrPicIdToggle;

  /* Breakdown of H264 Dummy Frames
   *
   * return new Uint8Array([
   *   0x0, 0x0, 0x0, 0x1,   // start code prefix
   *   0x27,                 // NAL unit type: 7: sequence parameter
   *     0x64 profile_idc,
   *     0x0 constraint_set_flags,
   *     0xd level_idc,
   *     SPS: [0xac, 0x57] seq_parameter_set_id,
   *     0x5 log2_max_frame_num_minus4,
   *     0x6 pic_order_cnt_type,
   *     0x64 log2_max_pic_order_cnt_lasb_minus4,
   *
   *  0x0, 0x0, 0x0, 0x1,    // start code prefix
   *  0x28,                  // NAL unit type: 8: picture parameter
   *    0xee, 0x3c, 0xb0,    // NAL unit payload
   *
   *  0x0, 0x0, 0x0, 0x1,    // start code prefix
   *  0x25,                  // NAL unit type: 5: IDR picture
   *    0xb8, ..._h264IdrPicIdToggle ? _DUMMY_H264_IDR_FRAME_SLICE_HDR0 : _DUMMY_H264_IDR_FRAME_SLICE_HDR1,
   *    0xff, 0x26, 0x1d, 0xd9, 0x18, 0xc0, 0xa1, 0x60, 0x0, 0x0, 0xc, 0xe5, 0xae, 0xa6, 0x6, 0x7, 0x14, 0x3, 0x54, 0x0, 0xf7, 0x60, 0xc1, 0xb5, 0xe5, 0x80, 0x0, 0x20, 0x20
   *
   * ])['buffer'];
   */

  return new Uint8Array([0x00, 0x00, 0x00, 0x01, 0x27, 0x64, 0x00, 0xd, 0xac, 0x57, 0x05, 0x06, 0x64, 0x00, 0x00, 0x00, 0x01, 0x28, 0xee, 0x3c, 0xb0, 0x00, 0x00, 0x00, 0x01, 0x25, 0xb8, ..._h264IdrPicIdToggle ? _DUMMY_H264_IDR_FRAME_SLICE_HDR0 : _DUMMY_H264_IDR_FRAME_SLICE_HDR1, 0xff, 0x26, 0x1d, 0xd9, 0x18, 0xc0, 0xa1, 0x60, 0x0, 0x0, 0xc, 0xe5, 0xae, 0xa6, 0x06, 0x07, 0x14, 0x03, 0x54, 0x00, 0xf7, 0x60, 0xc1, 0xb5, 0xe5, 0x80, 0x00, 0x20, 0x20])['buffer'];
}

